@import "../../../../assets/variables";

.burger{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 34px;
  left: 16px;
  width: 30px;
  height: 22px;
  z-index: 30;
  cursor: pointer;
  @media (max-width: $max-tablet) {
    width: 40px;
    left: 19px;
  }
  @media (max-width: $max-mobile) {
    top: 16px;
    width: 24px;
  }
  &__line{
    background-color: white;
    width: 100%;
    height: 3px;
    border-radius: 2px;
    transition: all 0.3s ease 0s;
    @media (max-width: $max-mobile) {
      background-color: black;
    }
  }
  &:before, &:after{
    content: '';
    background-color: white;
    width: 100%;
    height: 3px;
    border-radius: 2px;
    transition: all 0.3s ease 0s;
    @media (max-width: $max-mobile) {
      background-color: black;
    }
  }
  &--active{
    &:before{
      transform: rotate(45deg);
      position: absolute;
      top: 11px;
      background-color: white;
    }
    .burger__line{
      display: none;
    }
    &:after{
      transform: rotate(-45deg);
      position: absolute;
      top: 11px;
      background-color: white;
    }
  }
}
